import { FormControl, Autocomplete, TextField } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import _get from 'lodash.get';
import _uniqBy from 'lodash.uniqby';

const TAG_LIMITATION = 1;

export type Props = {
	statuses: Array<Object>;
	selectedStatuses: Array<number | string>;
	setSelectedStatuses: Function;
};

const useStyles = makeStyles(() => ({
	formControl: {
		width: '100%',
	},
}));

const StatusSelector = ({
	selectedStatuses,
	setSelectedStatuses,
	statuses,
}: Props) => {
	const classes = useStyles();
	return (
		<FormControl variant='outlined' className={classes.formControl}>
			<Autocomplete
				value={selectedStatuses}
				onChange={(event, newValues) => {
					setSelectedStatuses(_uniqBy(newValues, 'Id'));
				}}
				fullWidth
				multiple
				limitTags={TAG_LIMITATION}
				options={statuses}
				getOptionLabel={option => _get(option, 'Name', '')}
				defaultValue={[]}
				renderInput={params => (
					<TextField
						{...params}
						label='Status'
						variant='outlined'
						multiline={false}
					/>
				)}
			/>
		</FormControl>
	);
};

export default StatusSelector;
