import React from 'react';
import { Button, Grid } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/core/styles';
import SearchField from '../SearchField';
import type { Props as SearchFieldProps } from '../SearchField';
import StatusSelector from '../StatusSelector';
import type { Props as StatusSelectorProps } from '../StatusSelector';

export type Props = Omit<SearchFieldProps, 'handleReset'> &
	StatusSelectorProps & {
		onSearch: Function;
		onTriggerSearch: Function;
		openFilter?: Boolean;
	};

const useStyles = makeStyles(() => ({
	form: {
		width: '100%',
	},
}));

const SearchFilter = ({
	onSearch,
	onTriggerSearch,
	statuses,
	search,
	setSearch,
	selectedStatuses,
	setSelectedStatuses,
	openFilter = true,
}: Props) => {
	const classes = useStyles();

	const onSubmit = (e: React.SyntheticEvent) => {
		e.preventDefault();
		onSearch();
	};

	const filterStyleProps = {
		display: { xs: openFilter ? 'block' : 'none', lg: 'block' },
	};

	return (
		<form onSubmit={onSubmit} className={classes.form}>
			<Grid
				container
				direction='row'
				justifyContent='flex-start'
				alignItems='center'
				spacing={1}
			>
				<Grid item xs={12} lg={4}>
					<SearchField search={search} setSearch={setSearch} />
				</Grid>
				{!_isEmpty(statuses) && (
					<Grid item xs={12} lg={6} sx={filterStyleProps}>
						<StatusSelector
							selectedStatuses={selectedStatuses}
							setSelectedStatuses={setSelectedStatuses}
							statuses={statuses}
						/>
					</Grid>
				)}
				<Grid item xs={12} lg={1} sx={filterStyleProps}>
					<Button
						type='submit'
						variant='contained'
						color='primary'
						size='large'
						fullWidth
					>
						Search
					</Button>
				</Grid>
				<Grid item xs={12} lg={1} sx={filterStyleProps}>
					<Button
						type='button'
						variant='outlined'
						size='large'
						fullWidth
						onClick={() => {
							setSearch('');
							setSelectedStatuses([]);
							onTriggerSearch(false);
						}}
					>
						Reset
					</Button>
				</Grid>
			</Grid>
		</form>
	);
};

export default SearchFilter;
