import { useEffect, useState } from 'react';
import _get from 'lodash.get';

import { useQuery, useLazyQuery, gql } from '@apollo/client';
import { TOffer } from '../../../types';
import graphql from '../../../graphql';

import Loader from '../../../ui-component/Loader';
import config from '../../../config';
import OfferToolbar from './OfferToolbar';
import { getValidSearchQuery } from '../../../helpers/searchFilter';
import { useSnackBar } from '../../../hooks';
import OffersTable from './OffersTable';

const OfferList = () => {
	const showSnackBar = useSnackBar();

	const { loading, error, data, fetchMore } = useQuery(gql(graphql.getOffers), {
		variables: { PageIndex: 0, PageSize: config.pagination.limit },
		fetchPolicy: 'cache-first',
		notifyOnNetworkStatusChange: true,
	});

	const {
		loading: loadingStatuses,
		error: statusesError,
		data: statusesData,
	} = useQuery(gql(graphql.listOfferSteps));

	const [doSearch, setDoSearch] = useState(false);
	const [search, setSearch] = useState('');
	const [selectedStatuses, setSelectedStatuses] = useState([]);
	const [executeSearch, { loading: isSearching, data: searchData }] =
		useLazyQuery(gql(graphql.getOffers));

	const [rows, setRows] = useState<TOffer[]>([]);
	const [hasMore, setHasMore] = useState<boolean>(false);
	const [page, setPage] = useState<number>(0);

	useEffect(() => {
		const newData = doSearch ? searchData : data;
		const newCount = _get(newData, 'getOffers.Count', 0);
		const newTotal = _get(newData, 'getOffers.Total', 0);
		setHasMore(newCount < newTotal);
		setPage(_get(newData, 'getOffers.PageIndex') || 0);
		setRows(_get(newData, 'getOffers.Items') || []);
	}, [data, searchData, doSearch]);

	const isLoading = loading || isSearching;

	if ((isLoading || loadingStatuses) && !data) return <Loader />;

	if (error || statusesError) {
		showSnackBar({
			message: _get(error, 'message', 'Something went wrong'),
			alertSeverity: 'error',
		});
	}

	const searchHandler = (pageIndex = 0) => {
		const { query, statusIds } = getValidSearchQuery({
			search,
			selectedStatuses,
		});
		if (query) {
			executeSearch({
				variables: {
					PageIndex: pageIndex,
					PageSize: config.pagination.limit,
					Query: query.trim(),
					StepIds: statusIds,
				},
			});
		}
		setDoSearch(Boolean(query));
	};

	const loadMore = () => {
		if (doSearch) {
			searchHandler(page + 1);
			return;
		}
		fetchMore({
			variables: { PageIndex: page + 1, PageSize: config.pagination.limit },
		});
	};

	const statuses = _get(statusesData, ['listOfferSteps'], []).map(
		(status: Object) => ({
			Id: _get(status, 'id'),
			Name: _get(status, 'messages.advisor'),
		})
	);

	return (
		<>
			{isSearching && <Loader />}
			<OfferToolbar
				onSearch={searchHandler}
				onTriggerSearch={setDoSearch}
				statuses={statuses}
				search={search}
				setSearch={setSearch}
				selectedStatuses={selectedStatuses}
				setSelectedStatuses={setSelectedStatuses}
			/>
			<OffersTable
				offers={rows}
				isLoading={isLoading}
				hasMore={hasMore}
				loadMore={loadMore}
			/>
		</>
	);
};

export default OfferList;
