import { FormattedMessage } from 'react-intl';
import { NavItemType, NavItemTypeObject, TMenuItemProp } from '../types';

export const generatePropertyOfferMenuItem = (
	property: TMenuItemProp,
	offer: TMenuItemProp
): NavItemTypeObject => {
	const { id: propertyId, url: propertyUrl } = property;
	const { id: offerId, url: offerUrl } = offer;
	return {
		...generateMenuItem({ title: 'property', id: propertyId, url: propertyUrl }),
		type: 'collapse',
		children: [generateMenuItem({ title: 'offer', id: offerId, url: offerUrl })],
	};
};

export const generateMenuItem = (item: TMenuItemProp): NavItemType => {
	const { url, id, title } = item;
	return {
		id,
		title: <FormattedMessage id={title} values={{ id }} />,
		url,
		type: 'item',
	};
};

export const getBreadcrumbsNavigation = (
	menuItem: NavItemTypeObject,
	item?: NavItemTypeObject
) => {
	const navigation = {
		...menuItem,
		children: item ? [item] : menuItem.children,
	};

	return {
		items: [
			{
				type: 'group',
				children: [navigation],
			},
		],
	};
};
