import * as React from 'react';
import {
	TableCell,
	TableHead,
	TableRow,
	TableSortLabel,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';
import { SortTableHeadProps, HeadCell, TOffer } from '../../../types';
import { headCells } from './helper';
import { ORDER } from '../../../utils/table';

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
	cell: {
		whiteSpace: 'pre',
	},
}));

const ListHeader = ({
	order,
	orderBy,
	onRequestSort,
}: Partial<SortTableHeadProps<TOffer>>) => {
	const classes = useStyles();

	const isSorted = (headCell: HeadCell<TOffer>) =>
		orderBy && orderBy.id === headCell.id;

	return (
		<TableHead>
			<TableRow>
				{headCells.map(headCell => (
					<TableCell
						key={headCell.id}
						align='left'
						padding={headCell.disablePadding ? 'none' : 'normal'}
						sortDirection={isSorted(headCell) ? order : undefined}
						className={classes.cell}
					>
						{onRequestSort ? (
							<TableSortLabel
								active={isSorted(headCell)}
								direction={isSorted(headCell) ? order : ORDER.ASC}
								onClick={() => onRequestSort && onRequestSort(headCell)}
							>
								{headCell.label}
								{isSorted(headCell) ? (
									<span className={classes.sortSpan}>
										{order === ORDER.DESC ? 'sorted descending' : 'sorted ascending'}
									</span>
								) : null}
							</TableSortLabel>
						) : (
							headCell.label
						)}
					</TableCell>
				))}
				<TableCell align='center' sx={{ pr: 3 }}>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default ListHeader;
