import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';

type SearchQuery = {
	search: string;
	selectedStatuses: Array<Object>;
};

export const getValidSearchQuery = ({
	search,
	selectedStatuses,
}: SearchQuery) => {
	const validStatues = selectedStatuses.map(status => _get(status, 'Id'));
	const searchText = !_isEmpty(validStatues) && !search ? '*' : search;
	return {
		query: searchText,
		statusIds: _isEmpty(validStatues) ? undefined : validStatues,
	};
};
